"use client";

// src/components/Toast.jsx
// Toast = ÌôîÎ©¥Ïóê Ïû†Íπê ÎÇòÌÉÄÎÇ¨Îã§ ÏÇ¨ÎùºÏßÄÎäî ÏïåÎ¶º Î©îÏãúÏßÄ
// alert() ÎåÄÏã† ÏÇ¨Ïö©ÌïòÎäî Îçî ÏòàÏÅòÍ≥† ÏÇ¨Ïö©Ïûê ÏπúÌôîÏ†ÅÏù∏ Î∞©Î≤ï

import React, { createContext, useContext, useState, useCallback } from 'react';
import { CheckCircle2, XCircle, Info, AlertCircle, X } from 'lucide-react';

// Toast ÌÉÄÏûÖ: success(ÏÑ±Í≥µ), error(ÏóêÎü¨), info(Ï†ïÎ≥¥), warning(Í≤ΩÍ≥†)
const ToastContext = createContext(null);

// Toast Provider - Ïï± Ï†ÑÏ≤¥ÏóêÏÑú ToastÎ•º ÏÇ¨Ïö©Ìï† Ïàò ÏûàÍ≤å Ìï¥Ï£ºÎäî Ïª¥Ìè¨ÎÑåÌä∏
export const ToastProvider = ({ children }) => {
  const [toasts, setToasts] = useState([]);

  // Toast Î©îÏãúÏßÄ Ï∂îÍ∞Ä Ìï®Ïàò
  const addToast = useCallback((message, type = 'info', duration = 3000) => {
    const id = Date.now() + Math.random();
    const newToast = { id, message, type };
    
    setToasts(prev => [...prev, newToast]);

    // duration ÏãúÍ∞Ñ ÌõÑ ÏûêÎèôÏúºÎ°ú ÏÇ¨ÎùºÏßÄÍ≤å
    setTimeout(() => {
      setToasts(prev => prev.filter(toast => toast.id !== id));
    }, duration);
  }, []);

  // Ìé∏Ïùò Ìï®ÏàòÎì§ - ÏÇ¨Ïö©ÌïòÍ∏∞ ÏâΩÍ≤å
  const toast = {
    success: (message) => addToast(message, 'success'),
    error: (message) => addToast(message, 'error'),
    info: (message) => addToast(message, 'info'),
    warning: (message) => addToast(message, 'warning'),
  };

  // Toast Ï†úÍ±∞ Ìï®Ïàò
  const removeToast = useCallback((id) => {
    setToasts(prev => prev.filter(toast => toast.id !== id));
  }, []);

  return (
    <ToastContext.Provider value={{ toast, removeToast }}>
      {children}
      {/* Toast Î©îÏãúÏßÄÎì§ÏùÑ ÌôîÎ©¥Ïóê ÌëúÏãúÌïòÎäî ÏòÅÏó≠ */}
      <div className="fixed top-20 right-4 z-[200] space-y-2 pointer-events-none">
        {toasts.map((toast) => (
          <ToastItem key={toast.id} toast={toast} onRemove={removeToast} />
        ))}
      </div>
    </ToastContext.Provider>
  );
};

// Í∞úÎ≥Ñ Toast ÏïÑÏù¥ÌÖú Ïª¥Ìè¨ÎÑåÌä∏
const ToastItem = ({ toast, onRemove }) => {
  const { message, type } = toast;

  // ÌÉÄÏûÖÎ≥Ñ Ïä§ÌÉÄÏùº ÏÑ§Ï†ï
  const styles = {
    success: {
      bg: 'bg-green-50',
      border: 'border-green-200',
      text: 'text-green-800',
      icon: <CheckCircle2 size={20} className="text-green-600" />,
    },
    error: {
      bg: 'bg-red-50',
      border: 'border-red-200',
      text: 'text-red-800',
      icon: <XCircle size={20} className="text-red-600" />,
    },
    info: {
      bg: 'bg-blue-50',
      border: 'border-blue-200',
      text: 'text-blue-800',
      icon: <Info size={20} className="text-blue-600" />,
    },
    warning: {
      bg: 'bg-yellow-50',
      border: 'border-yellow-200',
      text: 'text-yellow-800',
      icon: <AlertCircle size={20} className="text-yellow-600" />,
    },
  };

  const style = styles[type] || styles.info;

  return (
    <div
      className={`
        ${style.bg} ${style.border} ${style.text}
        border rounded-xl shadow-lg p-4 min-w-[300px] max-w-[400px]
        flex items-start gap-3 animate-in slide-in-from-right fade-in
        pointer-events-auto
      `}
    >
      <div className="shrink-0 mt-0.5">{style.icon}</div>
      <div className="flex-1 text-sm font-medium">{message}</div>
      <button
        onClick={() => onRemove(toast.id)}
        className="shrink-0 p-1 hover:bg-black/5 rounded transition"
      >
        <X size={16} className={style.text} />
      </button>
    </div>
  );
};

// Hook - Îã§Î•∏ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÏâΩÍ≤å ÏÇ¨Ïö©ÌïòÍ∏∞ ÏúÑÌïú Ìï®Ïàò
export const useToast = () => {
  const context = useContext(ToastContext);
  if (!context) {
    throw new Error('useToast must be used within ToastProvider');
  }
  return context.toast;
};
